import {Box} from "@mui/material";
import {Link} from "react-router-dom";

export default function HomePage({}) {
    return <Box>
        <video id={"videoCover"} width={"100%"} autoPlay={true} controls>
            <source src="video/night.mp4" type="video/mp4"/>
        </video>
        <Box className={"descBlock"}>
            <Box>
                <p>
                    Culminant l'exprience d'une artiste plasticienne internationale et 2 mois de travail, LBGCA vous propose l'apoge du monde sculptural
                </p>
            </Box>
            <Box id="descImg1" className={"imgDesc"}>

            </Box>
        </Box>
        <Box className={"descBlock"}>
            <Box>
                <p>
                    Notre intention : vous proposer un Memento  la hauteur de vos richesses perdues
                </p>
            </Box>
            <Box id="descImg2" className={"imgDesc"}></Box>
        </Box>
        <Box className={"descBlock"}>
            <Box>
                <p>
                    Rsultant d'un processus impliquant alginate, silicone, pltre, rsine et peinture, nos nombreux essais ont aboutis  un dtail plus vrai que nature
                </p>
            </Box>
            <Box id="descImg3" className={"imgDesc"}>
            </Box>
        </Box>
        <Box className={"descBlock"}>
            <Box>
                <p>
                    Ce n'est donc pas une seule sculpture mais bien toute une gamme varie que LBGCA vous invite  dcouvrir dans notre section <Link to="/products">Les Oeufs du Magasin</Link>
                </p>
            </Box>
            <Box id="descImg4" className={"imgDesc"}></Box>
        </Box>
    </Box>
} 